*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/opensanssemibold.woff2") format("woff2"), url("../fonts/opensanssemibold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.main-page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  letter-spacing: -0.005em;
}

.main-page body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.header {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: #ffffff;
  min-height: 62px;
  position: absolute;
  background-color: transparent;
  top: 0;
  right: 0;
  left: 0;
  z-index: 20;
}
@media (max-width: 1023px) {
  .header {
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
    color: #48494d;
  }
}
@media (max-width: 1023px) {
  .header--no-js, .header--open-menu {
    display: block;
    position: static;
    background-color: #ffffff;
    min-height: 538px;
    padding: 0 0 30px;
    z-index: 10;
  }
}
@media (max-width: 1023px) {
  .header--open-menu {
    position: fixed;
    min-height: 100%;
    max-height: 100%;
    overflow-y: scroll;
  }
}

.header__list a {
  color: inherit;
  opacity: 0.7;
  text-decoration: none;
  padding: 10px 0;
}
.header__list a:hover {
  opacity: 1;
  color: #0ad9c6;
}
.header__list a:active {
  opacity: 0.6;
}

.header__nav {
  position: relative;
  max-width: 874px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .header__nav {
    display: none;
  }
}

@media (max-width: 1023px) {
  .header--no-js .header__nav,
.header--open-menu .header__nav {
    display: block;
    max-width: 100%;
    margin: 100px 0 0;
  }
}

.header__list {
  list-style: none;
  margin: 0;
  padding: 20px 20px 9px;
  display: flex;
  column-gap: 135px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  flex-wrap: wrap;
}

@media (max-width: 1023px) {
  .header--no-js .header__list,
.header--open-menu .header__list {
    padding: 0;
    border: none;
    flex-direction: column;
    row-gap: 30px;
  }
}

@media (max-width: 1023px) {
  .header--no-js li,
.header--open-menu li {
    background-color: #f6f6f6;
    min-height: 60px;
  }
}

@media (max-width: 1023px) {
  .header--no-js a,
.header--open-menu a {
    position: relative;
    display: block;
    padding: 15px;
  }
}
@media (max-width: 1023px) {
  .header--no-js a::after,
.header--open-menu a::after {
    position: absolute;
    content: "";
    width: 25px;
    height: 25px;
    top: 18px;
    right: 0;
    background-image: url("../img/icons/arrow.svg");
    background-repeat: no-repeat;
  }
}

.header__toggle-menu {
  display: none;
}
@media (max-width: 1023px) {
  .header__toggle-menu {
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    top: 0;
    right: 0;
    background-color: #0ad9c6;
    z-index: 30;
  }
}
.header__toggle-menu::after {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  top: 8px;
  right: 7px;
  background-image: url("../img/icons/menu.svg");
  background-repeat: no-repeat;
}

.header--no-js .header__toggle-menu,
.header--open-menu .header__toggle-menu {
  display: none;
}

@media (max-width: 1023px) {
  .header--open-menu .header__toggle-menu {
    display: block;
    background-color: transparent;
  }
  .header--open-menu .header__toggle-menu::after {
    width: 52px;
    height: 52px;
    top: -4px;
    right: -4px;
    background-image: url("../img/icons/close.svg");
  }
}

