.views__item {
  position: relative;
  min-height: 715px;
  z-index: 0;
}
@media (max-width: 1023px) {
  .views__item {
    min-height: 404px;
  }
}
@media (max-width: 767px) {
  .views__item {
    min-height: 396px;
  }
}
.views__item:first-child::before {
  position: absolute;
  content: "";
  width: 180px;
  height: 180px;
  left: -60px;
  top: 156px;
  background-image: url("../img/icons/ellipse.svg");
  background-repeat: no-repeat;
  z-index: -1;
}
@media (max-width: 1023px) {
  .views__item:first-child::before {
    width: 127px;
    height: 127px;
    background-size: 127px;
    left: -37px;
    top: 201px;
  }
}
@media (max-width: 1023px) and (max-width: 767px) {
  .views__item:first-child::before {
    left: -24px;
    top: 201px;
  }
}
.views__item:first-child + li {
  top: 30px;
}
@media (max-width: 1023px) {
  .views__item:first-child + li {
    top: 0;
  }
}
.views__item:first-child + li::before {
  position: absolute;
  content: "";
  width: 47px;
  height: 47px;
  left: -20px;
  top: -20px;
  background-image: url("../img/icons/rectangle.svg");
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .views__item:first-child + li::before {
    position: static;
  }
}
.views__item:first-child + li::after {
  position: absolute;
  content: "";
  width: 47px;
  height: 47px;
  right: -22px;
  top: 541px;
  background-image: url("../img/icons/rectangle.svg");
  background-repeat: no-repeat;
}
@media (max-width: 1023px) {
  .views__item:first-child + li::after {
    position: static;
  }
}
.views__item:last-child {
  top: -10px;
}
@media (max-width: 1023px) {
  .views__item:last-child {
    top: 0;
  }
}
.views__item:last-child::after {
  position: absolute;
  content: "";
  width: 95px;
  height: 95px;
  right: -36px;
  top: -32px;
  background-image: url("../img/icons/ellipse.svg");
  background-repeat: no-repeat;
  background-size: 95px;
}
@media (max-width: 1023px) {
  .views__item:last-child::after {
    width: 68px;
    height: 68px;
    right: -25px;
    top: -20px;
    background-size: 68px;
  }
}
@media (max-width: 767px) {
  .views__item:last-child::after {
    right: -15px;
  }
}

